.add-form {
	border: 2px solid #eee;
	float: left;
	margin: 0 1em 1em 0;
	padding: 1em;
}

.add-form { text-transform: uppercase; }

.list-box {
	border: 2px solid #fbebcf;
	float: left;
	font-size: 1.5em;
	margin: 0 1em 1em 0;
	max-width: 300px;
	padding: 1em;
	width: 100%;
}

.list-box h5 {
	font-size: 1em;
	margin-bottom: .5em;
}

.list-box ul {
	font-size: 1em;
	list-style-type: none;
	margin-bottom: .5em;
}

.list-box li {
	background-color: #f5d8a6;
	padding: .25em;
	margin-bottom: .2em;
}

.list-box li input { visibility: hidden; }
.list-box li:hover input { cursor: pointer; visibility: visible; }

.list-box { background-color: #fff; font-size: 1em; }
.list-box li { background-color: #fff; border-bottom: 1px solid #eee;}
.list-box input { border-bottom: none; border-left: none;  border-top: none; border-right: none; }
.list-box input[type=text] { border-bottom: 3px solid #fbebcf; }
.list-box input[type=submit] { cursor: pointer; }
.list-box input[type=submit]:disabled { color: #ddd; }



